.signup_mainDiv {
  display: flex;
  flex-direction: row;
  width: 90%;
  margin: auto;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 5px;
}

.signup_subDiv_inputs {
  margin: 20px;
  background-color: aliceblue;
  padding: 10px;
  border-radius: 20px;
  width: 150%;
}

.signup_subDiv_heading {
  margin: 10px;
  padding: 10px;
  font-size: 45px;
  font-weight: 900;
  color: rgb(24, 75, 130);
  margin-bottom: 0px;
  padding-bottom: 0px;
}

.signup_subDiv_subheading {
  margin: 10px;
  padding: 10px;
  font-size: 15px;
  font-weight: 400;
  color: rgb(62, 61, 61);
  margin-top: 0px;
  padding-top: 0px;
}

.signup_subDiv_option_button {
  display: flex;
  width: 90%;
  margin: auto;
  margin-top: 30px;
  margin-bottom: 30px;
}

button img {
  width: 35px;
  padding: 2px;
}

.signup_subDiv_option_signupBYGoogle,
.signup_subDiv_option_signupBYFacebook {
  display: flex;
  width: 100%;
  font-size: 13px;
  background-color: white;
  border: 1px solid rgb(41, 152, 248);
  padding: 5px;
  border-radius: 30px;
  width: 250px;
  align-items: center;
  margin: 10px;
}

.signup_subDiv_option_signupBYGoogle:hover,
.signup_subDiv_option_signupBYFacebook:hover {
  background-color: white;
  cursor: pointer;
}

.hr-text {
  line-height: 1em;
  position: relative;
  outline: 0;
  border: 0;
  color: black;
  text-align: center;
  height: 1.5em;
  opacity: 0.5;
  &:before {
    content: "";
    background: linear-gradient(to right, transparent, #818078, transparent);
    position: absolute;
    left: 0;
    top: 50%;
    width: 100%;
    height: 1px;
  }
  &:after {
    content: attr(data-content);
    position: relative;
    display: inline-block;
    color: black;
    padding: 0 0.5em;
    line-height: 1.5em;
    color: #3c3b3a;
    background-color: aliceblue;
  }
}

.signup_subDiv_form {
  margin: 20px;
  padding: 5px;
}

#phone,
#password {
  display: block;
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.signup_subDiv_img {
  width: 100%;
  margin: 10px;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 5px;
  background-color: rgb(76, 96, 247);
  border-radius: 20px;
}

.signup_subDiv_img_heading {
  color: white;
  font-size: 30px;
  font-weight: 900;
  margin: 20px;
}

.signup_subDiv_img_subheading {
  color: white;
  font-size: 14px;
  font-weight: 400;
  margin: 20px;
}

.signup_subDiv_img_side img {
  display: block;
  margin: auto;
  margin-top: 100px;
  width: 80%;
}

@media screen and (max-width: 990px) {
  .signup_subDiv_heading {
    font-size: 40px;
  }
  .signup_subDiv_img_heading {
    font-size: 20px;
  }
}

@media screen and (max-width: 990px) {
  .signup_subDiv_img {
    display: none;
  }
}

@media screen and (max-width: 640px) {
  .signup_mainDiv {
    width: 500px;
    margin: 10px;
  }
  .signup_subDiv_option_button {
    display: block;
  }
}

@media screen and (max-width: 510px) {
  .signup_subDiv_heading {
    font-size: 30px;
  }
  .signup_mainDiv {
    width: 400px;
    margin: auto;
  }
}

@media screen and (max-width: 410px) {
  .signup_mainDiv {
    width: 300px;
    margin: auto;
  }
}
