.contact_form {
  width: 90%;
  margin: auto;
  display: flex;
  margin: auto;
  border-radius: 20px;
}

.PhysicsSansar {
  color: rgb(24, 75, 130);
  font-size: 20px;
  font-weight: 700;
}

.links {
  /* color: rgb(121, 56, 238); */
  color: rgb(24, 75, 130);
  font-weight: 600;
}
.contact_form_sideBar {
  position: relative;
  background-color: aliceblue;
  padding: 10px 10px 10px 10px;
  margin: 20px 20px 20px 20px;
  border-radius: 20px;
  width: 40%;
  background-image: url("https://www.unbornnetworks.in/assets/landing/img/gallery/about2.png");
  background-repeat: no-repeat; /* Do not repeat the image */
}

.sub ul {
  display: flex;
  position: absolute;
  bottom: 10px;
  width: 90%;
  justify-content: space-around;
}
.contact_form_sideBar li {
  display: flex;
}

.contact_form_heading {
  font-size: 45px;
  font-weight: 900;
  color: rgb(24, 75, 130);
  padding: 20px;
  margin: 20px;
  margin-bottom: 0px;
  padding-bottom: 0px;
}

.contact_form_mainBar {
  background-color: aliceblue;
  padding: 10px 10px 10px 10px;
  margin: 20px 20px 20px 20px;
  border-radius: 20px;
  width: 100%;
}

.contact_form_subheading {
  color: rgb(64, 61, 61);
  font-weight: 600;
  padding: 20px;
  margin: 20px;
  margin-top: 0px;
  padding-top: 0px;
}

label {
  font-size: 14px;
}

input[type="text"],
input[type="email"],
textarea,
select {
  width: 100%;
  padding: 10px;
  font-size: 14px;
  border-radius: 4px;
  border: 1px solid #ccc;
  margin-bottom: 20px;
}

textarea {
  margin-bottom: 0px;
}

.form_group_message {
  margin: 20px 20px 20px 20px;
  padding: 20px 20px 20px 20px;
}
select {
  height: 38px;
}

.submit[type="submit"] {
  /* background-color:  rgb(121,56,238); */
  background-color: rgb(24, 75, 130);
  color: #fff;
  border: none;
  border-radius: 10px;
  font-size: 14px;
  cursor: pointer;
  width: 90%;
  height: 40px;
  transition: all 0.3s;
  font-weight: 700;
}

.submit:hover {
  background-color: rgb(38, 132, 255);
}

.submit {
  padding: 1px;
  margin: 20px 20px 20px 20px;
  margin-left: 30px;
}

@media (max-width: 930px) {
  .contact_form_sideBar {
    display: none;
  }
}

@media (max-width: 600px) {
  .contact_form_heading {
    font-size: 30px;
  }
  .contact_form_subheading {
    font-size: 18px;
    font-weight: 300;
  }
}
